<template>
  <div id="coupon">
    <header>
        <nav class="navbar">
            <button v-on:click="$router.go(-1)" class="navbar-toggler pb-0" type="button" data-toggle="collapse" data-target="#navbarToggleExternalContent" aria-controls="navbarToggleExternalContent" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-prev-icon"></span>
            </button>            
            <a class="page-title" href="#">優惠券</a>        
            <a class="pr-3" href="#">搜尋附近餐廳</a>
        </nav>
    </header>  
    <main :class="{ countdown: time > 0 }">       
        <img :src="coupons[this.$route.params.id][1]">
    </main>
    <footer>
        <ul class="nav bottom-nav">             
            <li class="nav-item">
                <a href="#" v-on:click="exchange" v-if="time == 0">兌換優惠</a>
                <a href="#" v-else class="countdown"><img class="clock-icon" src="images/img_coupon_clock_n.png">優惠倒數 {{ time | timeFormat }}</a>               
            </li>            
        </ul>
    </footer>
  </div>
</template>

<script>
export default {
    name: 'Coupon',
    props: ['coupons'],
    data() {
        return {
            time: 0
        }    
    },  
    mounted() {        
    },
    created() {    
    },
    methods: {
        exchange() {
            this.time = 120
            var clock = window.setInterval(() => {                
                if (this.time > 0) {
                    this.time--
                } else {
                    clearInterval(clock)
                }        
            },1000)
        }
    },
    filters: {
        timeFormat(time) {
            var m = parseInt(time/60)
            var s = parseInt(time%60)
            s = s.toString().padStart(2, "0")        
            return `${m}:${s}`
        }
    }
}
</script>

