<script>
export default {
    name: 'List',
    props: ['coupons'], 
    data() {
        return {        
        }    
    },
    computed: {
        deadline: function () {
            var date = new Date();
            date.setDate(date.getDate() + 1)
            var next_day = {
                year: date.getFullYear(),
                month: date.getMonth() + 1,
                day: date.getDate()
            }
            const now = Date.parse(new Date())
            const end = Date.parse(new Date(`${next_day.year}/${next_day.month}/${next_day.day}`))
            const msec = end - now      
            let day = parseInt(msec / 1000 / 60 / 60 / 24)
            let hr = parseInt(msec / 1000 / 60 / 60 % 24)                    
            return `期限：${next_day.year} 年 ${next_day.month} 月 ${next_day.day} 日&emsp;尚餘：${day} 天 ${hr} 小時`            
        }
    }  
}
</script>

